/* eslint-disable */
import React from "react";import MaterialTable from "material-table";// https://material-table.com/

class MTableHeader extends React.Component {state = { data: [],  columns: [  {title: "Gender", field: "gender",  defaultGroupSort: "desc"
        /** cellStyle: data => {if (data === "male") { return { color: "red" }; } else { return {  color: "blue"   };   }     }*/
      },
      {title: "Title", field: "name.title" },   { title: "First", field: "name.first" },
      {title: "Last",  field: "name.last",  cellStyle: { fontWeight: "Bold" }  },
      { title: "Email", field: "email" }    
]};
componentDidMount() {fetch("https://randomuser.me/api/?results=200&nat=us").then(Response => Response.json())
      .then(findresponse => { console.log(findresponse.results); this.setState({data: findresponse.results });  });  }
  render() {return (
<MaterialTable class="table" columns={this.state.columns} data={this.state.data} title="StudentTable" container direction="row" justify="center" alignItems="center"
        options={{columnsButton: true, exportButton: true, selection: true, headerStyle: { backgroundColor: "purple", color: "white" },  rowStyle: () => {}  }} />);}}

export default MTableHeader;